import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, Ncm } from '../../../../models/dtos/mlf/ncm';
import NcmGeneroIS from '../../../components/ger/ncmGeneroIS/NcmGeneroIS';
import AtividadeEconomicaIS from '../../../components/mlf/atividadeEconomicaIS/AtividadeEconomicaIS';

interface NcmEditViewProps extends EditViewProps<Ncm> {}

const NcmEditView: FC<NcmEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Ncm>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de Gênero NCM">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              id="codigo"
              label="Código"
              formProps={{ ...formProps, validation: { required: true, maxLength: 8 } }}
              isPrependLabel={true}
              placeholder="Informe o código"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              label="Descrição"
              placeholder="Informe a descrição"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <AtividadeEconomicaIS
              formProps={formProps}
              id="atividadeEconomica"
              isPrependLabel={true}
              label="Atividade Econômica"
              inputSearchProps={{ selectProps: { isMulti: false } }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <NcmGeneroIS
              formProps={formProps}
              id="genero"
              isPrependLabel={true}
              label="Gênero de N.C.M"
              inputSearchProps={{ selectProps: { isMulti: false } }}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default NcmEditView;
